<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="elevation-2">
        <v-card-title class="headline justify-center blue--text">
          {{ itemactual.referencia + ' - ' + itemactual.titulo }}
        </v-card-title>
        <v-card-text>
          <div style="padding-bottom:10px" v-if="editOk">
            <v-btn fab dark small color="primary" @click="dialog_addupd = true, edit=0,itemnov=''" >
              <v-icon dark>add</v-icon>
            </v-btn>
          </div>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
            >
          </v-text-field>
          <br>
          <v-data-table
           :headers="headers"
           :items="dataNovedades"
           :search="search"
           class="elevation-1"
          >
            <template v-slot:item.archivo="{ item }">
              <v-icon
                class="mr-5"
                color="primary"
                @click="openFile(item.rutasarchivo)"
                v-if="item.archivo != ''"
              >
                insert_drive_file
              </v-icon>
            </template>
            <template v-slot:item.accion="{ item }">
              <v-icon
                small
                class="mr-5"
                color="primary"
                @click="dialog_addupd = true, edit=1, itemnov=item"
                v-if="editOk"
              >
                edit
              </v-icon>
              <v-icon
                small
                class="mr-5"
                color="error"
                @click="dialog_del=true, itemnov=item"
                v-if="editOk"
              >
                delete
              </v-icon>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
     </v-flex>

     <v-dialog v-model="dialog_addupd" width="800">
      <v-card>
        <v-card-title>
          <span class="headline red--text">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-form v-model="validnov" ref="validnov">
            <v-flex>
              <v-text-field v-model="novedad"
                label="Novedad"
                :rules="[rules.required]"
              >
              </v-text-field>
            </v-flex>
            <v-flex>
              <span>Soporte</span>
              <adjuntarArchivos
                :item="archivo"
                :placeholder="'Soporte'"
                :tipoarchivo="'.pdf'"
              />
              <v-icon color="primary"
                @click="abrirDialogoElimFile(archivo.file_name)"
              >
                cancel
              </v-icon>
            </v-flex>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="dialog_addupd=false">Cancelar</v-btn>
          <v-btn color="primary" text @click="adicionarNovedad" :disabled="!validnov">Aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog_del" max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline red--text">Eliminar Novedad</span>
        </v-card-title>
        <v-card-text>
          ¿Esta seguro de eliminar la Novedad seleccionada?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="closeDialogoDel">Cancelar</v-btn>
          <v-btn color="red" text @click="deleteitem" >Eliminar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog_deletefile" width="550">
      <v-card>
          <v-card-title>
              <span class="headline red--text">Eliminar Archivo</span>
          </v-card-title>
          <v-card-text>
            ¿Esta seguro de eliminar el Archivo {{ filedelete }} definitivamente?.
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" dark text @click="dialog_deletefile=false">Cancelar</v-btn>
              <v-btn color="red" text @click="eliminarArchivo" >Eliminar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
   </v-container>
</template>
<script>
import { anunciosProcAdquisicion, novedadesProceso } from '@/services/api'
export default {
  name: 'anunciosTabNov',
  props: ['itemactual'],
  components: {
    adjuntarArchivos: () => import('@/components/AdjuntarArchivos')
  },
  data () {
    return {
      search: '',
      dataNovedades: [],
      novedad: '',
      dialog_addupd: false,
      formTitle: '',
      validnov: false,
      archivo: { file_name: '', file_path: '', file_name_tmp: '' },
      rutaarchivo: '',
      dialog_del: false,
      itemnov: '',
      edit: -1,
      dialog_deletefile: false,
      filedelete: '',
      codigonovedad: '',
      editOk: 0,
      headers: [
        { text: 'Descripción', align: 'left', value: 'descripcion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha', align: 'center', value: 'fecha', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Ver Doc.', align: 'center', value: 'archivo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Acción', align: 'center', value: 'accion', class: 'subtitle-1 font-weight-bold black--text' }
      ],
      rules: {
        required: value => !!value || 'Campo Requerido.'
      }
    }
  },
  methods: {
    async getNovedades () {
      const { data } = await anunciosProcAdquisicion.getNovedades({
        codigoproceso: this.itemactual.id
      })
      this.dataNovedades = data.data
    },
    limpiarForm () {
      this.codigonovedad = 0
      this.novedad = ''
      this.archivo = { file_name: '', file_path: '', file_name_tmp: '' }
      this.rutaarchivo = ''
    },
    openFile (url) {
      try {
        window.open(url, '_blank')
      } catch (error) {
        this.$toasted.error(error, { icon: 'error', duration: 5000 })
      }
    },
    closeDialogoDel () {
      this.itemnov = ''
      this.dialog_del = false
    },
    abrirDialogoElimFile (file) {
      this.filedelete = file
      // this.codigonovedad = codigonovedad
      this.dialog_deletefile = true
    },
    async adicionarNovedad () {
      const { data } = await novedadesProceso.addUpdNovedad({
        codigo: this.codigonovedad,
        codigoproceso: this.itemactual.id,
        descripcion: this.novedad,
        archivo: this.archivo
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'check', duration: 5000 })
          this.limpiarForm()
          this.edit = -1
        } else {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Se presentó un error al intentar agregar o actualizar la información', { icon: 'error', duration: 5000 })
      }
      this.getNovedades()
      this.dialog_deletefile = false
    },
    async eliminarArchivo () {
      const ruta = '\\novedadesprocesos\\NOV' + this.itemnov.codigo + '\\'
      const { data } = await novedadesProceso.eliminarArcNovedad({
        codigo: this.itemnov.codigo,
        ruta: ruta,
        archivo: this.filedelete
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'check', duration: 5000 })
          this.archivo = { file_name: '', file_path: '', file_name_tmp: '' }
        } else {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Se presentó un error al intentar eliminar el archivo', { icon: 'error', duration: 5000 })
      }
      this.getNovedades()
      this.dialog_deletefile = false
    },
    async deleteitem () {
      const ruta = '\\novedadesprocesos\\NOV' + this.itemnov.codigo + '\\'
      const { data } = await novedadesProceso.eliminarNovedad({
        codigo: this.itemnov.codigo,
        ruta: ruta,
        archivo: this.itemnov.archivo
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'check', duration: 5000 })
          this.archivo = { file_name: '', file_path: '', file_name_tmp: '' }
        } else {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Se presentó un error al intentar eliminar el archivo', { icon: 'error', duration: 5000 })
      }
      this.getNovedades()
      this.dialog_del = false
    }
  },
  watch: {
    itemactual: {
      handler: function (val) {
        if (val) {
          this.editOk = this.$session.get('codigopersonal') === val.ab_asignado
          this.getNovedades()
        }
      },
      immediate: true // provides initial (not changed yet) state
    },
    edit: {
      handler: function (val) {
        console.log('edit ' + val)
        if (val) {
          this.formTitle = 'Editar'
          this.codigonovedad = this.itemnov.codigo
          this.novedad = this.itemnov.descripcion
          const archivoactual = this.itemnov.archivo
          this.rutaarchivo = this.itemnov.rutasarchivo
          this.archivo = { file_name: archivoactual, file_path: (archivoactual !== '' ? this.rutaarchivo : ''), file_name_tmp: '' }
        } else {
          this.formTitle = 'Nuevo'
          this.limpiarForm()
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
    this.getNovedades()
  }
}
</script>
